import { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faRectangleXmark } from '@fortawesome/free-solid-svg-icons';
import cls from '../Navbar.module.scss';
import '../Navbar.css';
import { Link, useNavigate } from 'react-router-dom';
import { isMobile } from 'react-device-detect';
import { FLOWHERO } from '../../../../shared/utility';

export const Navbar = props => {
	const { background = 'white' } = props;
	const [nav, setNav] = useState(false);
	const navigate = useNavigate();



	const openNav = () => {
		setNav(!nav);
	};

	return (
		<div className={cls.navbarsection} style={{ position: 'fixed', backgroundColor: background, justifyContent: 'space-evenly' }}>
			<div className={cls.navbartitle} style={{ fontSize: isMobile ? '20px' : '26px', marginLeft: isMobile ? '0' : '50px' }}>
				{FLOWHERO(isMobile ? '26px' : '28px', 'var(--app-color-gray-dark)', 'bold')}
			</div>
			{/* Desktop */}
			<ul className={cls.navbaritems} style={{ display: isMobile ? 'none' : '', width: '100%', justifyContent: 'right', padding: '0 20px' }}>
				<li>
					<Link to="/#/home" className={cls.navbarlinks} style={{
						cursor: 'pointer',
						fontWeight: 'bold'
					}}>
						{`HOME`}
					</Link>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{
							cursor: 'pointer',
							fontWeight: 'bold'
						}}
						onClick={() => {
							window.scrollTo({ top: document.body.scrollHeight / 14, behavior: 'smooth' })
						}}
					>
						{'SOLUTIONS'}
					</a>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{
							cursor: 'pointer',
							fontWeight: 'bold'
						}}
						onClick={() => {

							window.scrollTo({ top: document.body.scrollHeight / 5.2, behavior: 'smooth' })
						}}
					>
						{'USE CASES'}
					</a>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{
							cursor: 'pointer',
							fontWeight: 'bold'
						}}
						onClick={() => {

							window.scrollTo({ top: document.body.scrollHeight / 1.26, behavior: 'smooth' })
						}}
					>
						{'LIBRARIES'}
					</a>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{
							cursor: 'pointer',
							fontWeight: 'bold'
						}}
						onClick={() => {
							window.scrollTo({ top: document.body.scrollHeight / 1.14, behavior: 'smooth' })
						}}
					>
						{'PLANS'}
					</a>
				</li>

				<li>
					<a href="#/register"
						className={cls.navbarlinks}
						style={{
							cursor: 'pointer',
							fontWeight: 'bold'
						}}
					>
						{`REGISTER`}
					</a>
				</li>
				<li>
					<a href="https://www.flowhero.app/#/login"
						className={cls.navbarlinks}
						style={{
							cursor: 'pointer',
							fontWeight: 'bold',
							color: 'green',
							border: '1px solid lightgray',
							padding: '10px'
						}}
					>
						{`LOGIN`}
					</a>
				</li>
				<li style={{ width: '20px' }} />
			</ul>


			{/* Mobile */}
			<div className={`mobile-navbar-menu ${nav ? 'open-nav' : ''}`} style={{ width: '80%', height: '100vh', backgroundColor: '', color: 'black', justifyContent: 'center', alignItems: 'flex-start' }}>
				<div onClick={openNav} className="mobile-navbar-close">
					<FontAwesomeIcon icon={faRectangleXmark} className="hamb-icon" style={{ color: 'var(--app-color-gray)', width: '25px', height: '25px' }} />
				</div>

				<ul className={cls.mobilenavbarlinks} >
					<li>
						<Link onClick={openNav} to="/" style={{ color: 'black' }}>
							{`HOME`}
						</Link>
					</li>

					<li>
						<a href="#/featured/sports" className={cls.navbarlinks} style={{ color: 'black' }}>
							{`PALARO`}
						</a>
					</li>

					<li>
						<a onClick={openNav} href="https://www.flowhero.app/#/login" style={{ color: 'black' }}>
							{`LOGIN`}
						</a>
					</li>

					
				</ul>
			</div>
			{/* Hamburger Icon */}
			<div className="mobile-nav" style={{ display: isMobile ? 'block' : 'none' }}>
				<FontAwesomeIcon icon={faBars} onClick={openNav} className="hamb-icon" />
			</div>
		</div>
	);
}

export default Navbar;
